$turquoise : #16B5C9;
$highlight: #ffda26;
// $highlight: #EDD83D;


body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    box-sizing: border-box;
    font-family: 'Proxima Nova Rg';
}

ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

h1,h2,h3,h4,h5,h6 {
    margin: 0;

    strong {
        font-family: 'Proxima Nova Lt';
    }
}

h3 {
    font-size: 48px;
    font-family: 'Proxima Nova Th';
    line-height: 1.1;
}

figure {
    margin: 0
}

a {
    text-decoration: none;
}

p {
    font-size: 20px;
    font-family: 'Proxima Nova Th';
    line-height: 1.65;
    margin: 0;
}

.container {
    max-width: 1100px;
    margin: auto;
}

.container_big {
    max-width: 1250px;
    margin: auto;
}

.banner {
    position: relative;
    width: 100%;
    height: 100vh;

    &::after {
        position: absolute;
        content: "";
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(0deg, #223335 5%, rgba(255, 255, 255, 0) 50%);
        z-index: 1;
    }

    video {
        width: 100%;
        height: 100%;
        object-fit: cover;

    }

    .text {
        position: absolute;
        top: 70%;
        width: 900px;
        max-width: 100%;
        z-index: 2;

        h1 {
            font-size: 72px;
            color: #fff;
            font-family: 'Proxima Nova Lt';
        }
    }
}

.abt {
    padding: 100px 0;
    width: 100%;
    overflow: hidden;
    background-color: #223335;

    .wrap {
        display: flex;
        flex-wrap: wrap;

        .textcard {
            width: 50%;
        }

        .image {
            width: 50%;


            svg {
                width: 100%;
                height: 640px;
                object-fit: cover;
                padding-left: 50px;
            }

        }

        .text {
            margin: 50px 0;
            margin-bottom: 200px;
            padding-right: 50px;

            &:last-child {
                margin-bottom: 0;
            }

            h3 {
                
                text-transform: uppercase;
                margin-bottom: 20px;
                color: #fdc150;

                strong {
                    color: #c95c60;
                }

            }

            p {
                color: #fff;
                width: 80%;
            }
        }
    }
}

.impacts {
    background-color: #22352b;
    min-height: 100vh;
    padding: 80px 0;
    position: relative;
    overflow: hidden;

    .impact_bg {
        position: absolute;
        top: 30%;
        left: 65%;
        transform: translate(-50%, -50%);
        z-index: 1;
        
        img {
            width: 680px;
            opacity: 0.3;
        }
    }

    .impact_head {
        color: #fff;

        h3 {
            text-transform: uppercase;
            color: #fdc150;
        }
        
        p {
            width: 450px;
            max-width: 100%;
            margin-top: 20px;
        }
    }

    .impact-nums {
        display: flex;
        flex-wrap: wrap;
        position: relative;
        padding-top: 50px;
        z-index: 2;
        justify-content: space-around;

        .icard {

            &:nth-child(2),&:nth-child(5),&:nth-child(7) {
                margin-top: -60px;
            }
            width: 33%;
            color: #fff;
            margin: 40px 0;
            text-align: center;

            .icon {
                img {
                    width: 120px;
                }
            }

        
            h2 {
                font-size: 78px;
                display: flex;
                align-items: center;
                justify-content: center;

                small {
                    font-size: 20px;
                    font-family: 'Proxima Nova Th';
                    margin-left: 16px;
                    line-height: 0;
                }
            }

            p {
                font-size: 16px;
                font-family: 'Proxima Nova Th';
                color: #b0b0b0;
            }
        }
    }
}

.objectives {
    background-color: #223335;
    padding: 100px 0 ;

    .full_image {
        margin-top: 80px;

        img {
            width: 100%;
            height: 640px;
            object-fit: cover;
            // clip-path: inset(15% 15% 15% 15%);

        }

        figcaption {
            color: #fff;
            margin-top: 20px;
            font-family: "Proxima Nova Th";
            font-size: 16px;
            font-weight: 100;
            max-width: 320px;
        }
    }

    .objective_head {
        color: #fff;
        width: 570px;
        max-width: 100%;
        margin: auto;
        margin-bottom: 70px;
        text-align: center;

        h3 {
            color: #fdc150;
            text-transform: uppercase;
            margin-bottom: 20px;

        }
    }

    .wrap {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        .card {
            width: 31.5%;
            height: 332px;
            transition: 0.8s;

            &:hover {
                transform: scale(1.1);
                cursor: pointer;

                .content {
                    transform: scale(0.9);
                }
            }

            .content {
                padding: 20px;
                display: flex;
                height: 88%;
                flex-flow: column;
                justify-content: space-between;
                position: relative;
                transition: 0.8s;
            }

            .svg {
                width: 50px;
            }

            h1 {
                position: absolute;
                font-size: 96px;
                font-weight: 100;
                color: #fff;
                font-family: "Proxima Nova Bl";
                opacity: 0.1;
                right: 15px;
                bottom: 0;
            }    

            a {
                color: #fff;
                font-weight: 100;
                font-family: "Proxima Nova Th";
            }

            h2 {
        
                font-family: "Proxima Nova Rg";
                font-size: 22px;
                max-width: 190px;
                color: #fff;
                text-transform: uppercase;
            }
            
        }
    }
}

.nepal {
    padding: 100px 0;
    background-color: #002846;

    svg {
        width: 100%;

        path {
            stroke: #2d74aa;
            opacity: 0.2;
            
            &.mdp {
                stroke: none;
                animation-duration: 2s;
                animation-name: pulse;
                transform-origin: 50% 50%;
                animation-iteration-count: infinite;
                opacity: 1;
                
            }

            &.markerdata {
                stroke: none;
                position: relative;
                opacity: 1;
                
                
                

            }
        
        }


    }
}

  
@keyframes pulse {
    from {
        box-shadow: 0 0 0 0 rgba(229, 62, 62, 1);
      transform: scale(0.95);
    }
    to {
      stroke-width: 0;
      stroke-opacity: 0;
      transform: scale(1);
      transform-origin: center center;
      box-shadow: 0 0 0 60px rgba(229, 62, 62, 0);
    }
  }

@media screen and (max-width: 480px) {
    h3 {
        font-size: 34px;
    }

    .container, .container_big {
        max-width: 95%;
        margin: auto;
    }

    .banner .text h1 {
        font-size: 55px;
    }

    .abt .wrap .textcard {
        width: 100%;
    }

    .abt .wrap .text {
        padding-right: 0;
        margin-bottom: 100px;
    }

    .abt .wrap .text p {
        width: 100%;
    }

    .abt .wrap .image {
        width: 100%;
    }

    .abt .wrap .image svg {
        padding-left: 0;
    }

    .impacts .impact-nums .icard {
        width: 100%;
    }

    .impacts .impact-nums .icard:nth-child(2), .impacts .impact-nums .icard:nth-child(5), .impacts .impact-nums .icard:nth-child(7) {
        margin-top: 40px;
    }

    .objectives .wrap .card {
        width: 100%;
        margin: 2px 0;
    }

    footer .footer_top .wrap {
        display: block;

        h4 {
            text-align: center;
        }
    }

    .social_icon {
        margin-top: 20px;
        text-align: center;
    }

    footer .footer_bottom .logo {
        justify-content: center;
    }

    footer .footer_bottom .wrap {
        display: block;
        text-align: center;

        .copyright {
            margin-top: 20px;
        }
    }
}